}
.hole-index .bgimg > div ul.list {
  width: 20px;
  transition: all 0.5s;
}
.hole-index .bgimg > div ul.list li {
  margin: 5px 0;
  font-size: 14px;
}
.hole-index .bgimg > div ul.list li:nth-child(2) span {
