  position: relative;
  width: 100vw;
  height: 100vh;
  background: url(../../assets/imgs/attack/bg.png);
  background-size: 100% 100%;

  .attack-link{
    width: 100%;
    position: absolute;
    top: 60px;
    left: 0;
    color: #fff;
    font-weight: bold;
    font-size: 25px;
    p:nth-child(1){
      position: absolute;
      left: 30%;
    }
    p:nth-child(2){
      position: absolute;
      right: 30%;
    }
  }

  .right-bottom{
    position: absolute;
    right: 30px;
    bottom: 30px;
    width: 440px;
    height: 400px;
    background: url(../../assets/imgs/index/dibu.png);
    background-size: 100% 100%;
    .title{
      height: 60px;
      line-height: 60px;
      color: #fff;
      border-bottom: 1px solid #053A93;
      span:nth-child(1){
        position: relative;
        top: 3px;
        margin-left: 30px;
        display: inline-block;
        width: 5px;
        height: 20px;
        background: #76FBFD;
        border-radius: 5px;
        margin-right: 10px;
      }
    }
  }

  .right-top{
    position: absolute;
    right: 30px;
    top: 100px;
    width: 440px;
    height: 400px;
    background: url(../../assets/imgs/index/dibu.png);
    background-size: 100% 100%;
    .title{
      height: 60px;
      line-height: 60px;
      color: #fff;
      border-bottom: 1px solid #053A93;
      span:nth-child(1){
        position: relative;
        top: 3px;
