        width: 5px;
        height: 20px;
        background: #76FBFD;
        border-radius: 5px;
        margin-right: 10px;
      }
    }
  }

  .bgimg{
    position: absolute;
    left: 50%;
    top: 50%;
    width: 1370px;
    height: 744px;
    background: url(../../assets/imgs/holeindex/qiu.png);
    background-size: 100% 100%;
    margin: -383px 0 0 -685px;
    >div ul.list{
      width: 20px;
      transition: all 0.5s;
    }
    >div ul.list li{
      margin: 5px 0;
      font-size: 14px;
    }
    >div ul.list li:nth-child(2) span{
      background: #004881;
    }
    >div ul.list li:nth-child(3) span{
      background: #84EDF3;
    }
    .bg-left-bottom{
      position: absolute;
      left: 300px;
      bottom: 100px;
      width: 220px;
      height: 240px;
      .border{
        position: absolute;
        left: 20px;
        top: 140px;
        transform: rotate(-60deg);
        -ms-transform:rotate(-60deg);
        .el-divider--horizontal{
          width: 50px;
          height: 1px;
        }
        .el-divider--horizontal:nth-child(2){
          transform:rotate(90deg);
          -ms-transform:rotate(90deg);
        }
        .el-divider--horizontal:nth-child(1){
          position: relative;
          left: 20px;
          top: -12px;
          transform:rotate(-30deg);
          -ms-transform:rotate(-30deg);
        }
      }
      ul{
        position: absolute;
        bottom: 0;
        right: 15px;
        width: 100px;
        li{
          position: relative;
          width: 100px;
          height: 20px;
          border: 1px solid #243787;
          color: #fff;
          text-align: center;
          span{
            display: inline-block;
            width: 20px;
            height: 20px;
            background: #357ADB;
            position: absolute;
            left: 0;
            top: 0;
          }
        }
      }
      .yuan{
        position: absolute;
        top: 15px;
        width: 100px;
        height: 100px;
        color: #14F8FE;
        text-align: center;
        line-height: 100px;
      }
      img{
        position: absolute;
        top: 0;
        left: 0;
      }
    }

