  .left-top{
    width: 500px;
    height: 190px;
    position: absolute;
    top: 100px;
    left: 30px;
    background: url(../../assets/imgs/index/dibu.png);
    background-size: 100% 100%;
    .list{
      margin-left: 15px;
      margin-top: 20px;
      .item:nth-child(2){
        background-image: url(../../assets/imgs/holeindex/huang.png);
        .num{
          color: #F7C638;
        }
        .value .color{
          background: #F7C638;
        }
      }
      .item:nth-child(3){
        background-image: url(../../assets/imgs/holeindex/cheng.png);
        .num{
          color: #EB7E65;
        }
        .value .color{
          background: #EB7E65;
        }
      }
      .item{
        display: inline-block;
        width: 135px;
        height: 80px;
        background: url(../../assets/imgs/holeindex/lv.png);
        background-size: 100% 100%;
        text-align: center;
        margin: 0 10px;
        .num{
          font-size: 20px;
          color: #72DDB3;
          font-weight: bold;
        }
        .txt{
          color: #fff;
          font-size: 12px;
          margin: 5px 0;
        }
        .value{
          position: relative;
          width: 90px;
          height: 15px;
          margin: 0 auto;
          border: 1px solid #fff;
          border-radius: 5px;
          .color{
            height: 15px;
            position: absolute;
            top: 0;
            left: 0;
            background: #72DDB3;
            transition: all .5s;
          }
        }
      }
    }
    .title{
      height: 60px;
