    top: 0;
    width: 100%;
    height: 80px;
    text-align: center;
    color: #f6f6f8;
    background: url(../../assets/imgs/index/head.png) no-repeat;
    background-size: 100%;
    background-position: bottom center;
    z-index: 10;
    .txt-center{
          h1{
            font-size: 35px;
            font-weight: 400;
          }
